import React from "react";
import  '../Booking.sektion.style.css'
import img1 from '../../../../icone/iconeSectionBooking/icone/1.png'
import img2 from '../../../../icone/iconeSectionBooking/icone/2.png'
import heartTrue from '../../../../icone/iconeSectionBooking/icone/heartTrue.png'
import heartFalse from '../../../../icone/iconeSectionBooking/icone/heartFalse.png'




export const PostsContainer = (props) => {
    return(
        <div  className='BookingFlexWrap'>
            {props.booking.map(cards =>
                <div className='BookingBackground'>
                <div className='BookingFlex'>
                    <p className='BookingMargin'>{cards.name}</p>
                    {cards.selected ?
                        <img className={'BookingImgSelected'} onClick={() => props.SelectedTrue(cards.id)} src={heartTrue}/> :
                        <img className={'BookingImgSelected'} onClick={() =>props.SelectedFalse(cards.id)} src={heartFalse}/>
                    }
                </div>
                <p className={'BookingSupTitle'}>Coupe</p>
                <div className={'BookingFlex'}>
                    <img className='BookingImgBackground' src={cards.carImg}/>
                </div>
                <div className='BookingFlex'>
                    <div className= 'BookingFlex'>
                        <img className='BookingImgIcon' src={img2}/>
                        <p className='BookingMargin'>{cards.users}</p>
                        <img className='BookingImgIcon' src={img1}/>
                        <p className='BookingMargin'>Manual</p>
                    </div>
                    <div>
                        <p className={'BookingPrice'}>{cards.price}</p>
                    </div>
                </div>
            </div>)}
        </div>
    )
}