import React from "react";
import {connect} from "react-redux";
import {PostsContainer} from "./Post.booking";
import AddBookingPost from "../add/Add.post";
import {
    addPostActionCreator,
    updateNewSelectedFalseActionCreator,
    updateNewSelectedTrueActionCreator
} from "../../../../Redux/bookingReducers/Booking.reducers";

const mapStateToProps = (state) => {
    return{
        booking: state.mainBookingSection.cardsBooking.post
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        SelectedFalse: (id) => {
            let action = updateNewSelectedFalseActionCreator(id)
            dispatch(action)
        },
        SelectedTrue: (id) => {
            let action = updateNewSelectedTrueActionCreator(id)
            dispatch(action)
        },
    }
}


const MapBookingCards = connect(mapStateToProps, mapDispatchToProps) (PostsContainer);
export default MapBookingCards;